.stars {
  padding: 0;
  display: flex;
  list-style: none;

  &__star {
    width: 16px;
    height: 16px;
    margin: 2px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  &__star:first-child {
    margin-left: 0;
  }

  &__star:last-child {
    margin-right: 0;
  }

  @for $rate from 1 through 5 {
    &--#{$rate} > :nth-child(-n + #{$rate}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
