:root {
  --column-count: 1;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 16px;
  transition: transform .3s;
  display: flex;
}

.card__img-block {
  margin-top: 16px;
}

.card__img {
  vertical-align: bottom;
  width: 160px;
  height: 134px;
}

.card__info {
  margin-top: 40px;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__code {
  color: #616070;
  margin-top: 4px;
}

.card__review-section {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 4px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  margin-top: 12px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-amount {
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  line-height: 40px;
  transition: background-color .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card__link {
  text-transform: uppercase;
  color: inherit;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: color .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  width: 40px;
  height: 40px;
  display: flex;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

body {
  margin: 0;
  padding: 0;
}

.stars {
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 2px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 > :nth-child(-n+1), .stars--2 > :nth-child(-n+2), .stars--3 > :nth-child(-n+3), .stars--4 > :nth-child(-n+4), .stars--5 > :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 10px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item:hover {
  cursor: pointer;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container {
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}
